



























body {
  font-family: Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
  font-size: 15px;
  color: #303133;
}
body a {
    color: #1890ff;
    text-decoration: none;
    font-size: 14px;
}
.el-divider--horizontal {
  margin: 0;
}
.home .sp {
  color: #1890ff;
}
.body_contain {
  width: 1000px;
  position: relative;
  margin: 70px auto 0 auto;
}
.body_contain .body_content {
    margin-top: 20px;
    position: relative;
}
.body_contain .body_content .body_right {
      position: absolute;
      top: 0;
      right: 0;
      width: 300px;
      height: 100px;
      -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
              box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      text-align: center;
}
.body_contain .body_content .body_left {
      position: relative;
      margin-right: 320px;
      -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
              box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.body_contain .body_content .body_left .info .info_title {
        height: 40px;
        line-height: 40px;
        border: 1px solid #e4e7ed;
}
.body_contain .body_content .body_left .info .info_title .info_lists {
          font-size: 14px;
          text-align: center;
          float: left;
          width: 44px;
          padding: 0 20px;
          border-right: 1px solid #e4e7ed;
          cursor: pointer;
}
.body_contain .body_content .body_left .info .info_content .info_block {
        margin: 20px;
}
.body_contain .body_content .body_left .info .info_content .info_block .title {
          height: 50px;
          line-height: 50px;
          font-weight: bolder;
          font-size: 16px;
          border-bottom: 1px solid #e4e7ed;
}
.body_contain .body_content .body_left .info .info_content .info_block .block {
          padding: 20px;
}
.body_contain .body_content .body_left .info .info_content .info_block .block .block_title {
            color: #8590a6;
            height: 34px;
            line-height: 34px;
}
.body_contain .body_content .body_left .info .info_content .info_block .block .block_title .sp_right {
              float: right;
}
.body_contain .body_content .body_left .info .info_content .info_block .block .block_content .block_content_left {
            height: 142px;
            width: 90px;
            float: left;
}
.body_contain .body_content .body_left .info .info_content .info_block .block .block_content .block_content_left img {
              height: 100%;
              width: 100%;
}
.body_contain .body_content .body_left .info .info_content .info_block .block .block_content .block_content_right {
            float: left;
            margin-left: 20px;
}
.body_contain .body_content .body_left .info .info_content .info_block .none {
          padding: 20px;
          height: 176px;
          text-align: center;
}
.body_contain .body_title {
    -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.body_contain .body_title .body_wrap .body_up {
      height: 132px;
      width: 100%;
      overflow: hidden;
}
.body_contain .body_title .body_wrap .body_up img {
        width: 100%;
}
.body_contain .body_title .body_wrap .body_down {
      position: relative;
}
.body_contain .body_title .body_wrap .body_down .people {
        float: left;
        z-index: 5;
        border: 4px solid white;
        position: absolute;
        top: -50px;
        left: 24px;
        border-radius: 8px;
}
.body_contain .body_title .body_wrap .body_down .people img {
          height: 160px;
          width: 160px;
}
.body_contain .body_title .body_wrap .body_down .text {
        height: 145px;
        position: relative;
        left: 230px;
}
.body_contain .body_title .body_wrap .body_down .text .tit1 {
          font-size: 22px;
          font-weight: bolder;
          height: 60px;
          line-height: 60px;
}
.head-contain {
  background: white;
  width: 100%;
  height: 52px;
  position: fixed;
  z-index: 4;
}
.head-contain .el-card__body {
    padding: 0;
}
.head-contain .header {
    position: relative;
    width: 1000px;
    margin: 0 auto;
}
.head-contain .header .header_info {
      margin: 0 20px;
      line-height: 52px;
}
.head-contain .header .search {
      display: inline-block;
      position: absolute;
      left: 30%;
      height: 52px;
      line-height: 52px;
}
.head-contain .header .search input {
        padding-left: 20px;
        border: 1px solid #dcdfe6;
        height: 28px;
        width: 300px;
        border-radius: 5px 0 0 5px;
}
.head-contain .header .search button {
        border-radius: 0 5px 5px 0;
}
.head-contain .header .person_info {
      position: absolute;
      right: 0;
      top: 52px;
      height: 100px;
      width: 100px;
      background: white;
}
.head-contain .header .person_info .keke {
        height: 30px;
        line-height: 30px;
        padding: 10px 20px;
        cursor: pointer;
}
.head-contain .header .people {
      float: right;
      height: 52px;
      width: 52px;
      border-radius: 50%;
      overflow: hidden;
}
.head-contain .header .people img {
        height: 100%;
        width: 100%;
}
